import React, { Suspense, lazy } from 'react';
import PropTypes from 'prop-types';
import ErrorBoundary from '../../../components/ErrorBoundary';
import style from '../style.scss';
import LoadingBar from '../../../components/LoadingBar';

const SuggestionItem = lazy( () => import(
	/* webpackChunkName: "apps/autosuggest-address/chunks/suggestion-item" */
	/* webpackMode: "lazy" */
	/* webpackPrefetch: true */
	'../components/SuggestionItem'
	)
);

const SuggestionResults = ( props ) => {
	const { addresses, onClick } = props;

	return (
		<ErrorBoundary>
			<div className={ `search-results-container ${ style.searchResultsContainer }` }>
				<p className={ `results-heading ${style.resultsHeading}` }>Found { addresses.length } matches:</p>
				<Suspense fallback={ <LoadingBar /> }>
					{ addresses.map( address => {

						return (
							<SuggestionItem key={ address.hash } onClick={ onClick } address={ address } />
						);
					} ) }
				</Suspense>
			</div>
		</ErrorBoundary>
	);
};

SuggestionResults.propTypes = {
	addresses : PropTypes.array,
	onClick   : PropTypes.func,
};

SuggestionResults.defaultProps = {
	addresses : [],
	onClick   : ( e ) => { alert( 'onClick() handler not implemented!' ); },
};

export default SuggestionResults;
